export default {
	data() {
		return {
			isShowUserInfo: false,
			canIUse: wx.canIUse('button.open-type.getUserInfo'),
			userInfo: {}
		}
	},
	onLoad() {
		wx.getSetting({
		  success: (res) => {
		    if (!res.authSetting['scope.record']) {
		      wx.authorize({
		        scope: 'scope.record',
		        success: () => {
		          // 用户已经同意小程序使用录音功能，后续调用 wx.startRecord 接口不会弹窗询问
		          wx.startRecord()
		        }
		      })
		    }
		  }
		})
		wx.getSetting({
			success: (res) => {
				console.log(res, 'asd')
				if (res.authSetting['scope.userInfo']) {
					// 已经授权，可以直接调用 getUserInfo 获取头像昵称
					wx.getUserInfo({
						success: (res) => {
							this.userInfo = res.userInfo;
							this.isShowUserInfo = true;
							console.log(res.userInfo)
						}
					})
				} else {
					wx.authorize({
					  scope: 'scope.userInfo',
					  success: () => {
					    // 用户已经同意小程序使用录音功能，后续调用 wx.startRecord 接口不会弹窗询问
							wx.getUserInfo({
								success: (res) => {
									this.userInfo = res.userInfo;
									this.isShowUserInfo = true;
									console.log(res.userInfo)
								}
							})
					    wx.startRecord()
					  }
					})
				}
			}
		})
	},
	onReady () {
		
	},
	methods: {
		bindGetUserInfo (e) {
			console.log(e.detail.userInfo)
		},
		getUserInfoButton () {
			console.log('asd')
			wx.getSetting({
				success: (res) => {
					console.log(res, 'asd')
					if (res.authSetting['scope.userInfo']) {
						// 已经授权，可以直接调用 getUserInfo 获取头像昵称
						wx.getUserInfo({
							success: (res) => {
								this.userInfo = res.userInfo;
								this.isShowUserInfo = true;
								console.log(res.userInfo)
							}
						})
					}
				}
			})
		},
		toTeach () {
			uni.navigateTo({
				url: '../teach/teach'
			})
		},
		toFeedback () {
			uni.navigateTo({
				url: '../feedback/feedback'
			})
		},
		toAbout () {
			uni.navigateTo({
				url: '../aabout/aabout'
			})
		},
	}
}
